<template>
	<div class="newCustomer-page input-border-none self-mt-header" v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <header class="post-head" v-show='!showTextarea'>
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">客户详情</span>
        </header>
        <header class="post-head" v-show='showTextarea'>
            <a href="javascript:;" class="back" @click="textareaBackList"></a> 
            <span class="head-font">企业简介</span>
        </header>
		<!-- <mt-header title="客户详情" fixed>
            <router-link to="/qdsCustomerManager" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
        <!-- <mt-header title="企业简介" fixed v-show='showTextarea'>
            <mt-button slot="left" icon="back" @click="textareaBackList"></mt-button>
        </mt-header> -->
        <div class="zhanwei"></div>
        <div class="infor-write" v-show="!showTextarea">
            <div>
                <van-cell-group >             
                    <van-field v-model="ruleCompanyAuth.companyAllName"  :disabled='disabled.companyAllName'  clearable label="企业全称"  placeholder="请输入企业全称" />
                    <van-field v-model="ruleCompanyAuth.companyTitle" :disabled='disabled.companyTitle'  clearable label="企业简称"  placeholder="请输入企业简称" />
                    <van-field v-model="ruleCompanyAuth.companyCode" :disabled='disabled.companyCode'   clearable label="社会信用代码"  placeholder="请输入统一社会信用代码" />
                    <van-field v-model="ruleCompanyAuth.selecteCity"  disabled  clearable label="企业地址"  placeholder="请输入企业地址" />
                    <van-field v-model="ruleCompanyAuth.companyAddress"  :disabled='disabled.companyAddress'  clearable label="详细地址" placeholder="请输入详细地址" />
                    <van-field v-model="ruleCompanyAuth.companysSize"  disabled  clearable label="企业规模"  placeholder="请选择企业规模"  />
                    <van-field v-model="ruleCompanyAuth.companysNature"   disabled clearable label="企业性质"  placeholder="请选择企业性质"  />
                    <van-field v-model="ruleCompanyAuth.companyIndustry"  disabled clearable label="所属行业"  placeholder="其选择所属行业"  />
                    <van-field   disabled clearable label="企业简介" v-model="ruleCompanyAuth.companyWorkCont" right-icon="arrow" placeholder="企业简介" @click-right-icon="getTextarea"  />
                </van-cell-group>
            </div>
            <div class="stepTwo">
                <div class="img-item">
                    <p>企业LOGO</p>
                    <div>
                        <img :src="ruleCompanyAuth.companyLogo" alt="" v-if="ruleCompanyAuth.companyLogo != ''" @click="getPreview(companyLogo)">
                    </div>
                    
                </div>
                <div class="img-item">
                    <p>企业封面图</p>
                    <div>
                        <img :src="ruleCompanyAuth.surfacePlotImg" alt="" v-if="ruleCompanyAuth.surfacePlotImg != ''" @click="getPreview(surfacePlotImg)">
                    </div>
                </div>
                <div class="img-item">
                    <p>企业实景图</p>
                    <div>
                        <img :src="ruleCompanyAuth.liveActionsImg[0]" v-if="ruleCompanyAuth.liveActionsImg.length > 0" alt="" @click="getPreview(ruleCompanyAuth.liveActionsImg)" :class="ruleCompanyAuth.liveActionsImg.length > 1 ? 'shadow-img' : ''">
                    </div>
                </div>
                <div class="img-item">
                    <p>与客户合作协议</p>
                    <div>
                        <img :src="ruleCompanyAuth.firstOrder[0]" v-if="ruleCompanyAuth.firstOrder.length > 0" alt="" @click="getPreview(ruleCompanyAuth.firstOrder)" :class="ruleCompanyAuth.firstOrder.length > 1 ? 'shadow-img' : ''">
                        <div class="file-box"  v-if="ruleCompanyAuth.firstOrder.length < 1">
                            <img :src="firstOrderFile" alt="" >
                            <p>上传成功的附件, 请到电脑网页版(b.youlanw.com)下载/预览</p>
                        </div>
                    </div>
                </div>
                <div class="zhanwei-bottom"></div>
                <div class="next-one">
                    <p @click="errorsBtn">报错</p>
                </div>
            </div>
        </div>
        <div v-show="showTextarea" class="text-box">
            <div class="zhanwei-top"></div>
            <mt-field placeholder="请填写企业简介" :disabled='disabled.companyWorkCont' type="textarea" rows="4" v-model="ruleCompanyAuth.companyWorkCont" class="text_area"></mt-field>
            <div class="textarea-tips"><p>不要填写电话、QQ、微信等联系方式</p></div>
            <!-- <div class="next-text">
                <p>完成</p>
            </div> -->
        </div>
         <div class="errors" v-show="selfLoading">
            <div class="errors-item">
                <p class="phone-num">021-22876396</p>
                <div class="errors-item-btn">
                    <p @click="cancelLoading">取消</p>
                    <p @click="cancelLoading"><a href="tel:021-22876396">呼叫</a></p>
                </div>
            </div>
        </div>
        
	</div>
  
</template>
<script>
import Util from '@/js/mUtils';
import { ImagePreview , Notify} from 'vant';
import { MessageBox } from 'mint-ui';
import textBg from "@/assets/qds-img/text_bg.jpg";
import {DLDict,DLArea,DLTypeList,DLNatureList,DLSizeList} from '@/js/dl-code';
export default {
	data() {
		return{
			globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loading: false,
			ruleCompanyAuth:{
                companyAllName: '',  // 企业全称
                companyTitle: '',   // 企业简称
                companyCode: '',     // 统一社会信用代码
                selecteCity:'',
                companyAddress:'',   // 详细地址
                companysSize:'',    // 企业规模
                companysNature:'',    // 企业性质
                companyIndustry:'',    // 所属行业
                companyWorkCont:'',   // 企业简介
                businessLicenseImg:'http://ylsite.oss-cn-hangzhou.aliyuncs.com/oms/businessLicenseImg/2019/3/25/18945_1553512616000.png',   // 营业执照
                companyLogo: '',
                surfacePlotImg:'',
                liveActionsImg: [],
                firstOrder:[]       //   一手订单/与客户合作协议图片
            },
            companyLogo: [],   // logo数组
            surfacePlotImg: [],   // logo数组
            disabled: {         // 如果该企业在该企业库中存在, 则后端返回企业信息, 可能存在空字段, 空字段可以修改, 不为空则不可修改
                companyAllName: true,
                companyTitle: true,
                companyCode: true,
                selecteCity: true,
                companyAddress: true,
                companysSize: true,
                companysNature: true,
                companyIndustry: true,
                companyWorkCont: true,
                businessLicenseImg: true,
                companyLogo: true,
                surfacePlotImg: true,
                liveActionsImg: true,
                firstOrder: true,
            },
            
            state: 2,     // 认证状态 
            customerId: '',
            showTextarea: false,
            qdsAuthState: '',  // 认证状态  
            clientHeight: {
				height: ""
            },
            selfLoading: false,
            textBg: textBg,
            firstOrderFile: '',
		}
	},
	mounted() {
        this.state = localStorage.getItem('qdsMcustomerStateValue')   // 客户的认证状态
        this.customerId = localStorage.getItem('qdsMcustomerCustomerId')   // 客户的id

        this.recertification()
	},
	components: {
        
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
	methods: {
         backHistory() {
            window.history.back(-1)
        },
        cancelLoading() {
            this.selfLoading = false;
        },
        errorsBtn() {
            this.selfLoading = true;
        },
        getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        beforeRouteLeave(to, from, next) {   // 返回列表页面保留滚动条高度
            // 设置下一个路由的 meta
            to.meta.keepAlive = true;  // 让列表页面缓存，即不刷新
            next();
        },
        textareaBackList() {
            this.showTextarea = false  // 关闭 textarea页面
        },
        getTextarea() {
            this.showTextarea = true
        },
        getPreview(imgArr) {   // 预览图片
            ImagePreview({
                images:imgArr,
                showIndex:true,
                showIndicators:true,
                loop:false,
                className:'del',
                onClose() {
               
                }
            })
        },
        recertification() {    // 重新认证 信息回显
                var that = this;
                var token = Util.getCookie('qdsMToken');
                that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/getCustomerInfo?token=" + token,   // that.qdsGlobalApiUrl  "http://10.0.15.58:19000/"
                    {
                        method: "get",
                        params: {
                            customerCorpId: that.customerId, // 客戶id, 从列表页面带过来的 
                            status: that.state,    // 状态
                        },
                    }
                ).then(function(response) {
                    if(response.data.code == 10000) {
                        var checkData = response.data.data.customerInfo
                        that.provinceVal = checkData.provinceCode   // 省code
                        that.cityVal = checkData.cityCode          // 市code
                        that.countyVal = checkData.countyCode      // 区code
                        var province = DLArea.getName(checkData.provinceCode)
                        var city = DLArea.getName(checkData.cityCode)
                        var county = DLArea.getName(checkData.countyCode)
                        var companysSize = ''
                        var companysNature = ''
                        var companyIndustry = ''
                        DLSizeList.forEach((item, index) =>{    // 企业规模
                            if(item.code == checkData.enterpriseScaleCode) {
                                companysSize = item.name
                                that.chanceScaleCode = checkData.enterpriseScaleCode
                            }
                        }) 
                        DLNatureList.forEach((item, index) =>{   // 企业性质
                            if(item.code == checkData.companyNatureCode) {
                                companysNature = item.value
                                that.chanceNatureCode = checkData.companyNatureCode
                            }
                        })
                        DLTypeList.forEach((item, index) =>{
                            if(item.code == checkData.industryCode) {
                                companyIndustry = item.name
                                that.chanceIndustryCode = checkData.industryCode
                            }
                        })
                        var  img = response.data.data.photos
                        if ([null, undefined, ''].indexOf(img.attachmentUrl) == -1) {         // 一手招聘订单 是否有值
                            var arr = img.attachmentUrl.split('.')
                            var index = arr.length -1
                            var type = arr[index]
                            if ('gif|jpg|jpeg|png|bmp|GIF|JPG|PNG'.indexOf(type) == -1) {    // 一手订单 非图片类型             
                                    that.firstOrderFile = that.textBg
                                    var firstOrderStr = []
                            }else {                                                          // 一手订单 是图片类型   
                                if(img.attachmentUrl.indexOf(',') !== -1){   // 多张图片
                                    var firstOrderStr = img.attachmentUrl.split(',')
								}else{   // 单张图片
									var firstOrderStr = [img.attachmentUrl]
								}         
                            }
                        } 

                        // var firstOrderStr = img.attachmentUrl.split(',')
                        that.ruleCompanyAuth = {
                            id: checkData.id,         // 重新认证id
                            corpCode: checkData.corpCode,
                            companyAllName: checkData.corpName,   // 企业全称
                            companyTitle: checkData.shortName,     // 企业简称
                            companyCode: checkData.creditCode,      // 统一社会信用代码 
                            selecteCity:province + '/' + city + '/' + county, // 企业地址
                            companyAddress:checkData.address,    // 详细地址
                            companysSize:companysSize,      // 企业规模
                            companysNature:companysNature,    // 企业性质
                            companyIndustry: companyIndustry,   // 所属行业
                            companyWorkCont:checkData.businessScope,
                            businessLicenseImg: img.businessLicenceUrl,
                            companyLogo: img.logoUrl,
                            surfacePlotImg:img.coverUrl,
                            liveActionsImg: img.environmentUrl,
                            firstOrder: firstOrderStr       //   一手订单/与客户合作协议图片
                        }
                        that.companyLogo.push(that.ruleCompanyAuth.companyLogo)
                        that.surfacePlotImg.push(that.ruleCompanyAuth.surfacePlotImg)
                    }else {
                        Notify({
                            message: response.data.message,
                            duration: 3000,
                            background: '#E6A23C'
                        });
                    }
                    
                })
                .catch(function(error) {
                    
                });
        },
 	}
}
</script>

<style scoped>
    .newCustomer-page {
        position: relative;
    }   
    .post-head{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2.6875rem;
        border-bottom: 1px solid #F5F5F5;
        background-color: #fff;
    }
    .back{
        position: absolute;
        left: .9375rem;
        display: inline-block;
        width: .5625rem;
        height: 1.1875rem;
        background: url(../../assets/post/back_icon.png);
        background-size: 100% 100%;
        cursor: pointer;
    }
    .head-font{
        font-size: 1rem;
        color: #0B231F;
        font-weight: bold;
    }
    .delete{ 
        z-index: 9999;
        position: absolute;
        bottom: -18.75rem;
        right: 46%;
        color: #fff;
        background: cyan;
        font-size: 1.875rem;
    }
    .zhanwei {
        width: 100%;
        height: 2.4375rem;
    }
    .zhanwei-top {
        width: 100%;
        height: .9375rem;
    }
    .zhanwei-bottom {
        width: 100%;
        height: 5rem;
    }
    .infor-write {
        padding-right: .9375rem;
        /* width: 100%; */
        height: 100%;
        background: #fff;
    }
    .text-box .textarea-tips {
        height: 2.0625rem;
        line-height: 2.0628rem;
        width: 21.5625rem;
        border: 1px solid #e5e5e5;
        border-bottom-right-radius:.8em;
        border-bottom-left-radius: .8em;
        margin: 0 auto;
    }
    .text-box .textarea-tips p{
        font-size: .75rem;
        color: #818D8D;
        padding-left: .9375rem;
    }
    .text_area {
        border-top-left-radius:.8em;
        border-top-right-radius:.8em;
        margin: 0 auto;
        height: 8.0625rem;
        width: 21.5625rem;
    }
    
    .next-one {
        position: fixed;
        bottom: 1px;
        padding: 0 .9375rem;
        background: #fff;
        border-top: 1px solid #e5e5e5;
    }
    .next-text {

        margin: 1.875rem .9375rem;
    }
    .hidden-input {
        display: none;
    }
    .next-one p{
        width: 21.5625rem;
        height: 2.75rem;
        line-height: 2.75rem;
        text-align: center;
        font-size: 1rem;
        color: #fff;
        margin: .4375rem 0;
        background: #FF963D;
        border-radius: 1.375rem;
        letter-spacing: .2em;
    }
    .next-text p{
        width: 21.5625rem;
        height: 2.75rem;
        line-height: 2.75rem;
        text-align: center;
        /* padding: 0 9.3125rem; */
        font-size: 1rem;
        color: #fff;
        background: #FF963D;
        border-radius: 1.375rem;
    }
    .newCustomer-page .stepTwo {
        width: 100%;
        background: #fff;
    }
    .newCustomer-page .stepTwo .img-item{
        height: 5.4375rem;
        margin: 0 .9375rem;
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        border-bottom: 1px solid #E5E5E5;
    }
    .newCustomer-page .stepTwo .img-item p{
        width: 34%;
        text-align: left;
        color: #0B231F;
        font-size: .875rem;
    }
    .newCustomer-page .stepTwo .img-item img{
        width: 3.5rem;
        margin-right: .625rem;
    }
    .newCustomer-page .stepTwo .img-item-album{
        position: relative;
    }
    .newCustomer-page .stepTwo .img-item-album .boder-none{
        border-bottom: none;
    }
    .newCustomer-page .stepTwo .img-item-album .line{
        position: absolute;
        top: 5.4375rem;
        right: .9375rem;
        height: 1px;
        width: 61%;
        background: #E5E5E5;
    }
    .newCustomer-page .stepTwo .img-item-album img{
        width: 3.5rem;
    }
    
    .newCustomer-page .step-three{
        width: 100%;
        background: #fff;
        height: 100%;
    }
    .newCustomer-page .step-three .cont{
        padding-top: 30%;
    }
    .newCustomer-page .step-three .cont img{
        width: 2.5rem;
    }
    .newCustomer-page .step-three .cont p:nth-child(2){
        font-size: 1.125rem;
        color: #0B231F;
        line-height: 3.625rem;
    }
    .newCustomer-page .step-three .cont p:nth-child(3){
        font-size: .875rem;
        color: #818D8D;
    }
    .newCustomer-page .step-three .cont p:nth-child(4){
        margin-top: 3.25rem;
        font-size: .875rem;
        color: #818D8D;
    }
    .newCustomer-page .step-three .cont p:nth-child(5){
        font-size: .875rem;
        color: #818D8D;
    }



    /* 图片裁剪样式 */
    .welfare-popup{
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 11;
        width: 100%;
        background-color: #fff;
    }
    .cut-img-box{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 87%;
        background-color: #fff;
    }
    .cut-img-box .cropper{
        margin-top: 3rem;
        width: 100%;
        height: 100%;
    }
    .big-img-business-popup{
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 11;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .big-img-business-popup .big-img{
        width: 100%;
    }
    .big-img-business-popup img{
        width: 100%;
    }
    .welfare-title{
        position: fixed;
        top: 0;
        left: 0;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 3.125rem;
        line-height: 3.125rem;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
        justify-content: space-between;
        background-color: #F0F0F0;
    }
    .welfare-title .van-picker__cancel{
        padding: 0 .9375rem;
        font-size: 1rem;
        color: #0B231F;
    }
    .welfare-title .van-picker__confirm{
        padding: 0 .9375rem;
        font-size: 1rem;
        color: #2DB7AC;
    }
    .shadow-img{
         box-shadow: 10px 10px 10px rgba(0,0,0,.5);
        -moz-box-shadow: 10px 10px 10px rgba(0,0,0,.5);
        -webkit-box-shadow: 10px 10px 10px rgba(0,0,0,.5);
    }


    /* 呼叫电话 */
     .errors{
        position: fixed;
        top: 0;
        z-index: 99;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, .6)
    }
    .errors .errors-item {
        position: absolute;
        top: 40%;
        left: 22%;
        width: 13.4375rem;
        /* height: 7.0625rem; */
