    }
    .errors .errors-item .errors-item-btn {
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    .errors .errors-item .errors-item-btn p:nth-child(1){
        font-size: 1rem;
        color: #818D8D;
        background: #E5E5E5;
