        line-height: 2.3rem;
        width: 100%;
        border-bottom-right-radius: .5rem;
    }
    .file-box {
        display: -webkit-flex;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
    }
    .newCustomer-page .stepTwo .img-item .file-box p {
        font-size: .625rem;
        color: #ec5751;
        width: 54%;
    }
</style>




